import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import Item from "./Item";
import ErrorBoundary from "./ErrorBoundary";

class ItemList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            returnAnimationClass: "",
            goBack: false,
            itemAnimationClass: "slide-in-animation",
            defaultAnimationClass: "fade-in-animation",
            clickable: false,
        };
    }

    componentDidMount() {
        setTimeout(() => {
            this.setState({ clickable: true });
        }, 1500);
    }
    getImage(path) {
        const images = require.context("../../public/images", true);

        const image = images("./" + path + ".png");

        return image;
    }

    goBack = () => {
        this.setState({
            defaultAnimationClass: "fade-out-animation",
            returnAnimationClass: "pop-out-animation",
            itemAnimationClass: "fade-out-animation",
        });
        setTimeout(() => {
            this.setState({ goBack: true });
        }, 1000);
    };

    goToDetails = () => {
        this.setState({
            defaultAnimationClass: "fade-out-animation",
            returnAnimationClass: "fade-out-animation",
            itemAnimationClass: "fade-out-animation",
        });
    };
    render() {
        const itemList = [
            { flavor: "Bananowy", image: "banana", price: 8 },
            { flavor: "Czekoladowy", image: "chocolate", price: 8 },
            { flavor: "Limonka z cytryn", image: "lemon", price: 8 },
            { flavor: "Truskawkowy", image: "strawberry", price: 8 },
            { flavor: "Wata cukrowa", image: "cotton-candy", price: 8 },
            { flavor: "mietanka", image: "cookie", price: 8 },
            { flavor: "Guma balonowa", image: "bubblegum", price: 8 },
            { flavor: "Mango", image: "mango", price: 8 },
        ];

        let items = itemList.map((item, index) => (
            <ErrorBoundary key={index}>
                <Item
                    flavor={item.flavor}
                    image={item.image}
                    price={item.price}
                    itemAnimationClass={this.state.itemAnimationClass}
                    goToDetails={this.goToDetails}
                    clickable={this.state.clickable}
                />
            </ErrorBoundary>
        ));

        if (this.state.goBack) {
            return <Redirect push to="/" />;
        }
        return (
            <div className="item-list">
                <div className={"header " + this.state.defaultAnimationClass}>
                    Wybierz smak
                </div>
                <div className="items">{items}</div>
                <div
                    className={
                        "return-button " + this.state.returnAnimationClass
                    }
                    onClick={this.goBack}
                >
                    <img src={this.getImage("return")} alt="return" />
                </div>
            </div>
        );
    }
}

export default ItemList;



