import React, { Component } from "react";
import { Redirect } from "react-router-dom";

class Item extends Component {
    constructor(props) {
        super(props);
        this.state = {
            redirect: false,
            isSelected: false,
        };
    }

    getImage(path) {
        const images = require.context("../../public/images", true);

        const image = images("./" + path + ".png");

        return image;
    }

    goToDetails = () => {
        if (this.props.clickable) {
            this.setState({ isSelected: true });
            this.props.goToDetails();
            setTimeout(() => {
                this.setState({ redirect: true });
            }, 1000);
        }
    };

    render() {
        if (this.state.redirect) {
            return (
                <Redirect
                    push
                    to={{
                        pathname: "/selectedItem",
                        state: {
                            image: this.props.image,
                            flavor: this.props.flavor,
                            price: this.props.price,
                        },
                    }}
                />
            );
        }
        if (this.state.isSelected) {
            return (
                <div className="item pop-out-animation">
                    <div className="select-product" onClick={this.goToDetails}>
                        <img
                            src={this.getImage(this.props.image)}
                            alt="product"
                            width="350"
                        />
                        <div className="flavor">{this.props.flavor}</div>
                    </div>
                </div>
            );
        }
        return (
            <div className={"item " + this.props.itemAnimationClass}>
                <div className="select-product" onClick={this.goToDetails}>
                    <img
                        src={this.getImage(this.props.image)}
                        alt="product"
                        width="350"
                    />
                    <div className="flavor">{this.props.flavor}</div>
                </div>
            </div>
        );
    }
}

export default Item;



