import React, { Component } from "react";
import { Redirect } from "react-router-dom";

class SelectedItem extends Component {
    constructor(props) {
        super(props);
        this.state = {
            goToSelectPayment: false,
            goBack: false,
            defaultAnimationClass: "fade-in-animation",
            returnAnimationClass: "fade-in-animation",
            paymentAnimationClass: "fade-in-animation",
        };
    }

    getImage(path) {
        const images = require.context("../../public/images", true);

        const image = images("./" + path + ".png");

        return image;
    }

    goToSelectPayment = () => {
        this.setState({
            returnAnimationClass: "fade-out-animation",
            defaultAnimationClass: "fade-out-animation",
            paymentAnimationClass: "pop-out-animation",
        });
        setTimeout(() => {
            this.setState({ goToSelectPayment: true });
        }, 1000);
    };

    goBack = () => {
        this.setState({
            returnAnimationClass: "pop-out-animation",
            defaultAnimationClass: "fade-out-animation",
            paymentAnimationClass: "fade-out-animation",
        });
        setTimeout(() => {
            this.setState({ goBack: true });
        }, 1000);
    };

    render() {
        if (this.state.goBack) {
            return <Redirect push to="/itemList" />;
        }
        if (this.state.goToSelectPayment) {
            return (
                <Redirect
                    push
                    to={{
                        pathname: "/selectPayment",
                        state: {
                            price: this.props.location.state.price,
                        },
                    }}
                />
            );
        }

        if (this.props.location.state === undefined)
            return <div>nie ma nic</div>;
        else
            return (
                <div className="selected-item">
                    <div className={this.state.defaultAnimationClass}>
                        <div className="header">Wybrany smak</div>
                        <img
                            src={this.getImage(
                                //this.props.location.state.image + "-selected"
                                "strawberry-selected"
                            )}
							alt="product"	
							width="600"						
                        />
                        <div className="flavor">
                            {this.props.location.state.flavor}
                        </div>
                    </div>
                    <div className="two-holder">
                        <div
                            className={
                                "price " + this.state.defaultAnimationClass
                            }
                        >
                            Cena {this.props.location.state.price} z
                        </div>
                        <div
                            className={
                                "select-payment " +
                                this.state.paymentAnimationClass
                            }
                            onClick={this.goToSelectPayment}
                        >
                            <img src={this.getImage("pay")} alt="pay" width="400"/>
                        </div>
                    </div>
                    <div
                        onClick={this.goBack}
                        className={
                            "return-button " + this.state.returnAnimationClass
                        }
                    >
                        <img src={this.getImage("return")} alt="return" />
                    </div>
                </div>
            );
    }
}

export default SelectedItem;



