export function fetchTemperature() {
    const temperature = fetch(
        `http://192.168.88.251:5000/sensors_state/temperature/fridge`
    )
        .then((response) => response.json())
        .then((data) => {
            return data.temperature;
        })
        .catch((err) => console.log(err));

    return temperature;
}

export function fetchProductsQuantity() {
    const productList = fetch(
        `http://192.168.88.251:5000/hmi/get_products`
    )
        .then((response) => response.json())
        .then((data) => {
            return data;
        })
        .catch((err) => console.log(err));

    return productList;
}

export function beginCardPayment() {
    const data = fetch(
        `http://192.168.88.251:5000/pay/start`
    )
        .then((response) => response.json())
        .then((data) => {
            return data;
        })
        .catch((err) => console.log(err));

    return data;
}

export function checkPaymentStatus() {
    const status = fetch(
        `http://192.168.88.251:5000/pay/status`
    )
        .then((response) => response.json())
        .then((data) => {
            return data;
        })
        .catch((err) => console.log(err));

    return status;
}



