import React, { Component } from "react";
import { Redirect } from "react-router";
import { beginCardPayment } from "../API";

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            redirect: false,
            defaultAnimationClass: "fade-in-animation",
        };
    }

    goToItemList = () => {
        this.setState({ defaultAnimationClass: "slide-out-animation" });
        setTimeout(() => {
            this.setState({ redirect: true });
        }, 1000);
    };

    async componentDidMount() {
        await beginCardPayment()
            .then((data) => {
                console.log(data);
            })
            .catch((err) => {
                console.log(err);
            });
    }
    render() {
        if (this.state.redirect) {
            return <Redirect push to="/itemList" />;
        }
        return (
            <div
                className={"home " + this.state.defaultAnimationClass}
                to="/itemList"
                onClick={this.goToItemList}
            >
                Dotknij <br /> aby rozpocz
            </div>
        );
    }
}

export default Home;



