import React, { Component } from "react";
import "./css/app.css";
import "./css/animations.css";
import { HashRouter as Router, Switch, Route } from "react-router-dom";
import Home from "./components/Home";
import ItemList from "./components/ItemList";
import SelectPayment from "./components/SelectPayment";
import SelectedItem from "./components/SelectedItem";
import Maintenance from "./components/Maintenance";
import ErrorBoundary from "./components/ErrorBoundary";
//import { fetchTemperature, fetchProductsQuantity } from "./API";

class App extends Component {
    constructor(props) {
        super(props);
        this.state = { temperature: "Loading temperature ..." };
    }

    /* async componentDidMount() {
        setInterval(() => {
            fetchTemperature()
                .then((temperature) => {
                    this.setState({
                        temperature: "Temp: " + temperature + " C",
                    });
                    console.log(temperature);
                })
                .catch((err) => {
                    console.log(err);
                });
        }, 5000);

        await fetchProductsQuantity()
            .then((data) => {
                console.log(data);
            })
            .catch((err) => {
                console.log(err);
            });
    } */

    render() {
        return (
            <Router>
                <div className="app">
                    <Switch>
                        <Route exact path="/" component={Home} />
                        <Route
                            path="/itemList"
                            render={(props) => (
                                <ErrorBoundary>
                                    <ItemList {...props} />
                                </ErrorBoundary>
                            )}
                        />
                        <Route
                            path="/selectedItem"
                            render={(props) => (
                                <ErrorBoundary>
                                    <SelectedItem {...props} />
                                </ErrorBoundary>
                            )}
                        />
                        <Route
                            path="/selectPayment"
                            render={(props) => (
                                <ErrorBoundary>
                                    <SelectPayment {...props} />
                                </ErrorBoundary>
                            )}
                        />
                        <Route
                            path="/maintenance"
                            render={(props) => (
                                <ErrorBoundary>
                                    <Maintenance {...props} />
                                </ErrorBoundary>
                            )}
                        />
                        <Route
                            path="/transaction"
                            render={(props) => (
                                <ErrorBoundary>
                                    <summary>
                                        Przetwarzanie platnosci prosz czeka
                                    </summary>
                                    <details style={{ whiteSpace: "pre-wrap" }}>
                                        Method: {props.location.state.method}
                                        Amount: {props.location.state.amount}
                                    </details>
                                </ErrorBoundary>
                            )}
                        />
                    </Switch>
                </div>
            </Router>
        );
    }
}

export default App;



