import React, { Component } from "react";
import { Redirect } from "react-router-dom";

class SelectPayment extends Component {
    constructor(props) {
        super(props);
        this.state = {
            payWithCash: false,
            payWithCard: false,
            goBack: false,
            returnAnimationClass: "fade-in-animation",
            defaultAnimationClass: "fade-in-animation",
            cashAnimationClass: "fade-in-animation",
            cardAnimationClass: "fade-in-animation",
        };
    }

    goToSelectPayment(method) {
        if (method === "cash") {
            this.setState({
                returnAnimationClass: "fade-out-animation",
                defaultAnimationClass: "fade-out-animation",
                cashAnimationClass: "pop-out-animation",
                cardAnimationClass: "fade-out-animation",
            });
            setTimeout(() => {
                this.setState({ payWithCash: true });
            }, 1000);
        }
        if (method === "card") {
            this.setState({
                returnAnimationClass: "fade-out-animation",
                defaultAnimationClass: "fade-out-animation",
                cashAnimationClass: "fade-out-animation",
                cardAnimationClass: "pop-out-animation",
            });
            setTimeout(() => {
                this.setState({ payWithCard: true });
            }, 1000);
        }
    }

    getImage(path) {
        const images = require.context("../../public/images", true);

        const image = images("./" + path + ".png");

        return image;
    }

    goBack = () => {
        this.setState({
            returnAnimationClass: "pop-out-animation",
            defaultAnimationClass: "fade-out-animation",
            cardAnimationClass: "fade-out-animation",
            cashAnimationClass: "fade-out-animation",
        });
        setTimeout(() => {
            this.setState({ goBack: true });
        }, 1000);
    };

    render() {
        if (this.state.goBack) {
            return <Redirect push to="/itemList" />;
        }
        if (this.state.payWithCard) {
            return (
                <Redirect
                    push
                    to={{
                        pathname: "/transaction",
                        state: {
                            method: "card",
                            amount: this.props.location.state.price,
                        },
                    }}
                />
            );
        }
        if (this.state.payWithCash) {
            return (
                <Redirect
                    push
                    to={{
                        pathname: "/transaction",
                        state: {
                            method: "card",
                            amount: this.props.location.state.price,
                        },
                    }}
                />
            );
        }
        return (
            <div className="select-payment">
                <div className={"header " + this.state.defaultAnimationClass}>
                    Wybierz metod patnoci
                </div>
                <div className="two-holder">
                    <div
                        className={
                            "transaction " + this.state.cashAnimationClass
                        }
                        onClick={() => {
                            this.goToSelectPayment("cash");
                        }}
                    >
                        <img
                            src={this.getImage("cash")}
                            alt="pay"
                            width="500"
                        />
                    </div>
                    <div
                        className={
                            "transaction " + this.state.cardAnimationClass
                        }
                        onClick={() => {
                            this.goToSelectPayment("card");
                        }}
                    >
                        <img
                            src={this.getImage("card")}
                            alt="pay"
                            width="540"
                        />
                    </div>
                </div>
                <div
                    className={
                        "return-button " + this.state.returnAnimationClass
                    }
                    onClick={this.goBack}
                >
                    <img src={this.getImage("return")} alt="return" />
                </div>
            </div>
        );
    }
}

export default SelectPayment;



